.popular__container{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
}

.popular__container h1{
    font-family: var(--font-family);
    color: white;
    font-size: 40px;
    font-weight: 1000;
    margin-bottom: 1rem;
    justify-content: left;
}

.popular__contents{
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
}